* {
  margin: 0;
  padding: 0;
}

h1 {
  color: #fff;
  background-color: #000;
  text-align: center;
  padding: 20px;
}

h2 {
  color: gray;
}

ul li {
  text-align: center;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px dashed #ddd;
  cursor: pointer;
}
ul li:hover {background-color: #ddd;}